:root {
  --layout-padding: 32px;
  --layout-width: calc(1120px + var(--layout-padding) * 2);
  --font-main: "Inter", sans-serif;
  --font-accent: "Poppins", sans-serif;
  --font-secondary: "Space Grotesk", sans-serif;
  --clr-primary: #000;
  --clr-var-black: #121212;
  --clr-white: #fff;
  --clr-secondary-blue: #377DFF;
  --clr-secondary-orange: #FFAB00;
  --clr-secondary-green: #38CB89;
  --clr-secondary-orange-light: #FFC95C;
  --clr-neutral-07: #141718;
  --clr-neutral-04: #6C7275;
  --clr-neutral-03: #E8ECEF;
  --clr-neutral-02: #F3F5F7;
  --clr-neutral-01: #FEFEFE;
  --clr-text-light: #fff;
  --clr-btn-focus-light: var(--clr-neutral-01);
  --clr-btn-primary: var(--clr-neutral-07);
  --clr-tagline-back: var(--clr-neutral-07);
  --clr-buy-link: var(--clr-secondary-orange);
  --clr-main-nav-back: var(--clr-secondary-orange-light);
  --clr-main-nav-text-dark: var(--clr-neutral-07);
  --clr-icons-black: var(--clr-neutral-07);
  --clr-main-nav-badge-back: var(--clr-neutral-07);
  --clr-main-nav-hamburger-active: #b89347;
  --clr-heading-accent: var(--clr-secondary-blue);
  --card-min-width: 16.375rem;
  --card-mini-width: 8.75rem;
  --clr-card-image-back: var(--clr-neutral-02);
  --clr-card-like-back: var(--clr-white);
  --clr-card-like-stroke: var(--clr-neutral-04);
  --clr-card-like-fill: var(--clr-primary);
  --clr-card-text: var(--clr-neutral-07);
  --clr-badge-back-default: var(--clr-white);
  --clr-badge-text-dark: var(--clr-neutral-07);
  --clr-badge-text-light: var(--clr-text-light);
  --clr-badge-success: var(--clr-secondary-green);
  --clr-dots-inactive-back: var(--clr-neutral-04);
  --clr-dots-active: var(--clr-neutral-07);
  --clr-plate-back: var(--clr-neutral-02);
  --clr-counter-back: #260100;
  --clr-counter-text: var(--clr-var-black);
  --clr-values-icon-stroke: var(--clr-neutral-07);
  --clr-values-back: var(--clr-neutral-02);
  --clr-values-heading: var(--clr-neutral-07);
  --clr-values-subheading: var(--clr-neutral-04);
  --clr-newsletter-back: #e1e4e4;
  --clr-newsletter-text: var(--clr-var-black);
  --clr-newsletter-form-text: var(--clr-neutral-04);
  --clr-newsletter-mail-icon: var(--clr-var-black);
  --clr-newsletter-form-underline: #A5A9AA;
  --clr-footer-back: var(--clr-neutral-07);
  --clr-specific-text: var(--clr-neutral-03);
  --swiper-theme-color: var(--clr-neutral-07) !important;
}

body {
  font-family: var(--font-main);
  text-wrap: balance;
}

*, *::after, *::before {
  box-sizing: border-box;
}

.container-right {
  padding-left: calc((100% - var(--layout-width)) / 2 + var(--layout-padding));
}
@media (max-width: 1200px) {
  .container-right {
    padding-left: var(--layout-padding);
  }
}

.container {
  max-width: var(--layout-width);
  padding: 0 var(--layout-padding);
  margin: 0 auto;
}

img {
  max-width: 100%;
  vertical-align: top;
}

.inter-regular {
  font-family: var(--font-main);
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.inter-bold {
  font-family: var(--font-main);
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
}

.inter-semi-bold {
  font-family: var(--font-main);
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
}

.inter-medium {
  font-family: var(--font-main);
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
}

h4 {
