.arrivals {
  padding: 3rem 0;
}
@media (max-width: 600px) {
  .arrivals {
    padding: 2rem 0;
  }
}
.arrivals__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 3rem;
}
@media (max-width: 600px) {
  .arrivals__row {
    margin-bottom: 2.5rem;
  }
}
.arrivals__header {
  margin-top: 0;
  margin-bottom: 0;
  font-family: var(--font-accent);
  font-weight: 500;
  font-size: 2.5rem;
  line-height: 1.1;
  letter-spacing: -0.4px;
}
@media (max-width: 600px) {
  .arrivals__header {
    font-size: 2.125rem;
    line-height: 1.1176470588;
  }
}
@media (max-width: 600px) {
  .arrivals .card {
    min-width: 14.4375rem;
  }
}

.badge {
  display: inline-block;
  padding: 0.25em 0.875em;
  margin-bottom: 0.5rem;
  background-color: var(--clr-badge-back-default);
  border-radius: 4px;
  color: var(--clr-badge-text-dark);
  font-size: 1rem;
  line-height: 1;
  font-weight: 700;
  text-align: center;
  white-space: nowrap;
  text-transform: uppercase;
}
.badge--success {
  background-color: var(--clr-badge-success);
  color: var(--clr-badge-text-light);
}

.bestseller {
  padding-bottom: 6.25rem;
}
.bestseller__heading {
  font-family: var(--font-accent);
  font-weight: 500;
  font-size: 2.5rem;
  line-height: 1.1;
  letter-spacing: -0.4px;
}
@media (max-width: 600px) {
  .bestseller__heading {
    margin-top: 0;
    margin-bottom: 1rem;
    text-align: center;
  }
}
.bestseller .cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(var(--card-min-width), 1fr));
  gap: 3rem 1.5rem;
}
@media (max-width: 900px) {
  .bestseller .cards {
    gap: 1rem 0.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .bestseller .cards .card {
    min-width: var(--card-mini-width);
  }
}
@media (max-width: 600px) {
  .bestseller {
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
  }
}

.btn {
  background-color: var(--clr-btn-primary);
  border: 1px solid var(--clr-btn-primary);
  color: var(--clr-text-light);
  padding: 0.75em 3.5em;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
.btn:hover {
  border: 1px solid var(--clr-btn-primary);
  background-color: transparent;
  color: var(--clr-btn-primary);
}
.btn:active {
  opacity: 0.5;
}
.btn:focus-visible {
  outline: 2px solid var(--clr-btn-focus-light);
}
.btn--m {
  padding: 0.6666666667em 3.1111111111em;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.7777777778;
}
.btn--s {
  padding: 0.375em 2.5em;
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.75;
}
.btn--to-cart {
  width: 100%;
}
@media (max-width: 600px) {
  .btn--to-cart {
    padding: 0.5em 1.6666666667em;
    font-size: 0.75rem;
    font-weight: 500;
    line-height: 2.3333333333;
  }
}

.btn-txt {
  color: var(--clr-neutral-07);
  all: unset;
  cursor: pointer;
  border-bottom: 1px solid var(--clr-neutral-07);
}
.btn-txt--s {
  font-size: 1rem;
  line-height: 1.75;
  font-weight: 500;
  letter-spacing: -0.4px;
}
.btn-txt--with-icon {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}
.btn-txt--with-icon .svg-icons--arrow-right-dims {
  stroke: var(--clr-neutral-07);
}

.card {
  min-width: var(--card-min-width);
  display: grid;
  gap: 0.75rem;
  user-select: none;
}
.card img {
  object-fit: cover;
  aspect-ratio: 262/349;
}
.card:hover .card__to-cart {
  opacity: 1;
  transform: translateY(-1rem);
}
.card:hover .card__to-cart .btn--to-cart {
  pointer-events: all;
}
.card:hover .card__like {
  opacity: 1;
  pointer-events: all;
}
.card__image-wrap {
  position: relative;
}
.card__image {
  text-align: center;
  background-color: var(--clr-card-image-back);
}
.card__row {
  display: flex;
  justify-content: end;
}
.card__badges {
  position: absolute;
  top: 1rem;
  left: 1rem;
}
.card__to-cart {
  opacity: 0;
  position: absolute;
  bottom: 0;
  left: 1rem;
  right: 1rem;
  transition: all 0.1s ease-in;
}
.card__to-cart .btn--to-cart {
  pointer-events: none;
}
@media (max-width: 600px) {
  .card__to-cart {
    display: none;
  }
}
.card__like {
  all: unset;
  position: absolute;
  top: 1rem;
  right: 1rem;
  opacity: 0;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--clr-card-like-back);
  border: none;
  box-shadow: 0px 8px 16px -8px rgba(15, 15, 15, 0.12);
  cursor: pointer;
  pointer-events: none;
}
@media (max-width: 600px) {
  .card__like {
    display: none;
  }
}
.card__desc {
  display: grid;
  gap: 0.25rem;
}
.card__title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card__title a {
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.625;
  text-decoration: none;
  color: var(--clr-card-text);
}
.card__brand {
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.625;
  color: var(--clr-card-text);
}
.card__price {
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.5714285714;
  color: var(--clr-card-text);
}

.collection {
  padding-bottom: 2.5rem;
}
@media (max-width: 600px) {
  .collection {
    padding-top: 2.5rem;
  }
}
.collection__sets {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}
@media (max-width: 600px) {
  .collection__sets {
    grid-template-columns: 1fr;
    grid-auto-rows: 1fr;
