  font-weight: 500;
  font-size: 2.5rem;
  line-height: 1.1;
  letter-spacing: -0.4px;
}
@media (max-width: 600px) {
  .collection__heading {
    text-align: center;
    font-size: 2.125rem;
    letter-spacing: -0.6px;
